/* Forms: Noemalize
----------------------------------------------------------------- */

/* 1. Correct margin displayed oddly in IE 6/7. */
form {
	margin: $form-margin; /* 1 */
	/* burf */
	font-family: $font-family-base;
	font-size: $font-size-base;
}
/* Define consistent border, margin, and padding. */
fieldset {
	border: $form-fieldset-border;
	margin: $form-fieldset-margin;
	padding: $form-fieldset-padding;
}
/* 1. Correct text not wrapping in Firefox 3.
   2. Correct alignment displayed oddly in IE 6/7. */
legend {
	white-space: normal; /* 1 */
	*margin-left: -7px; /* 2 */
	/* burf */
	display: block;
	width: 100%;
	margin: $form-legend-margin;
	padding: $form-legend-padding;
	border: $form-legend-border;
	border-width: $form-legend-borderWidth;
	font-weight: 700;
}
label {
	/* burf */
	margin: $form-label-margin;
}
/* 1. Correct font size not being inherited in all browsers.
   2. Address margins set differently in IE 6/7, Firefox 3+, Safari 5, and Chrome.
   3. Improve appearance and consistency in all browsers.  */
button,
input,
select,
textarea {
	font-size: 100%; /* 1 */
	margin: 0; /* 2 */
	vertical-align: baseline; /* 3 */
	*vertical-align: middle; /* 3 */
}
/* Address Firefox 3+ setting `line-height` on `input` using ` !important` in the UA stylesheet. */
button,
input {
	line-height: normal;
}
/* Address inconsistent `text-transform` inheritance for `button` and `select`. All other form control elements do not inherit `text-transform` values. Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+. Correct `select` style inheritance in Firefox 4+ and Opera. */
button,
select {
	text-transform: none;
}
/* 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio` and `video` controls.
   2. Removed from normalize.css: "Correct inability to style clickable `input` types in iOS which added -webkit-appearance: button."
   3. Improve usability and consistency of cursor style between image-type `input` and others.
   4. Remove card spacing in IE 7 without affecting normal text inputs. Known issue:card spacing remains in IE 6.
   5. Remove WebKit default button business. */
button,
html input[type="button"], /* 1 */
input[type="reset"],
input[type="submit"] {
	@extend .button;
	cursor: pointer; /* 3 */
	*overflow: visible; /* 4 */
	-webkit-appearance: none; /* 5 */
}
/* Re-set default cursor for disabled elements. */
button[disabled],
html input[disabled] {
	cursor: default;
}
/* 1. Address box sizing set to content-box in IE 8/9.
   2. Remove excess padding in IE 8/9.
   3. Remove excess padding in IE 7. Known issue: excess padding remains in IE 6. */
input[type="checkbox"],
input[type="radio"] {
	box-sizing: border-box; /* 1 */
	padding: 0; /* 2 */
	*height: 13px; /* 3 */
	*width: 13px; /* 3 */
	margin-right: 4px;
}
/* 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
   2. Removed from normalize.css (Address `box-sizing` set to `border-box` in Safari 5 and Chrome (include `-moz` to future-proof)). */
input[type="search"] {
	-webkit-appearance: textfield; /* 1 */
}
/* Remove card padding and search cancel button in Safari 5 and Chrome on OS X. */
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
	-webkit-appearance: none;
}
/* Remove card padding and border in Firefox 3+. */
button::-moz-focus-card,
input::-moz-focus-card {
	border: 0;
	padding: 0;
}
/* 1. Remove default vertical scrollbar in IE 6/7/8/9.
   2. Improve readability and alignment in all browsers. */
textarea {
	overflow: auto; /* 1 */
	vertical-align: top; /* 2 */
}

/* Forms: Style (adapted from Pure CSS v0.5.0)
----------------------------------------------------------------- */

input[type="text"],
input[type="password"],
input[type="email"],
input[type="url"],
input[type="date"],
input[type="month"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="week"],
input[type="number"],
input[type="search"],
input[type="tel"],
input[type="color"],
select,
textarea {
	padding: $form-input-padding;
	display: inline-block;
	border: $form-input-border;
	box-shadow: $form-input-shadow;
	border-radius: $form-input-borderRadius;
  color: $color-base;
}
input[type="text"]:focus,
input[type="password"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="time"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="week"]:focus,
input[type="number"]:focus,
input[type="search"]:focus,
input[type="tel"]:focus,
input[type="color"]:focus,
select:focus,
textarea:focus {
	outline: 0;
	outline: thin dotted \9; /* IE6-9 */
	border-color: $color-input-focusBorder;
	outline: 1px auto $color-input-focusBorder;
	box-shadow: $form-input-focusShadow;
}
input[type="file"]:focus,
input[type="radio"]:focus,
input[type="checkbox"]:focus {
	outline: thin dotted $color-border;
	outline: 1px auto $color-input-focusBorder;
	box-shadow: $form-input-focusShadow;
}
.checkbox,
.radio {
	margin: 0.5em 0;
	display: block;
}
input[type="text"][disabled],
input[type="password"][disabled],
input[type="email"][disabled],
input[type="url"][disabled],
input[type="date"][disabled],
input[type="month"][disabled],
input[type="time"][disabled],
input[type="datetime"][disabled],
input[type="datetime-local"][disabled],
input[type="week"][disabled],
input[type="number"][disabled],
input[type="search"][disabled],
input[type="tel"][disabled],
input[type="color"][disabled],
select[disabled],
textarea[disabled] {
	cursor: not-allowed;
	background-color: #eaeded;
	color: #cad2d3;
}
input[readonly],
select[readonly],
textarea[readonly] {
	background: #eee;
	color: #777;
	border-color: #ccc;
}
input:focus:invalid,
textarea:focus:invalid,
select:focus:invalid {
	color: #b94a48;
	border-color: #ee5f5b;
}
input:focus:invalid:focus,
textarea:focus:invalid:focus,
select:focus:invalid:focus {
	border-color: #e9322d;
}
input[type="file"]:focus:invalid:focus,
input[type="radio"]:focus:invalid:focus,
input[type="checkbox"]:focus:invalid:focus {
	outline-color: #e9322d;
}
select {
	border: 1px solid #ccc;
	background-color: white;
}
select[multiple] {
	height: auto;
}
.form-row {
	margin: $form-row-margin;
}
.form-controls {
	margin: 20px 0 0;
}
.required {
	color: #c00 !important;
}
/* Form Stacked */
.form-stacked input[type="text"],
.form-stacked input[type="password"],
.form-stacked input[type="email"],
.form-stacked input[type="url"],
.form-stacked input[type="date"],
.form-stacked input[type="month"],
.form-stacked input[type="time"],
.form-stacked input[type="datetime"],
.form-stacked input[type="datetime-local"],
.form-stacked input[type="week"],
.form-stacked input[type="number"],
.form-stacked input[type="search"],
.form-stacked input[type="tel"],
.form-stacked input[type="color"],
.form-stacked select,
.form-stacked label,
.form-stacked textarea,
/* Aligned forms should stack on small screens */
.form-aligned select,
.form-aligned label,
.form-aligned textarea {
	display: block;
	margin: $form-input-margin;
}

/* Aligned Forms */
@include mq(max, $bp--mobile) {
	.form-aligned input,
	.form-aligned textarea,
	.form-aligned select {
		display: inline-block;
		*display: inline;
		*zoom: 1;
		vertical-align: middle;
	}
	.form-aligned textarea {
		vertical-align: top;
	}
	.form-aligned .form-row label {
		display: inline-block;
		width: 200px;
		margin: 0 20px 0 0;
		vertical-align: middle;
		text-align: right;
	}
	.form-aligned .form-row-checkbox,
	.form-aligned .form-row-radio {
		margin:  $form-row-margin;
		margin-left: 220px;
	}
	.form-aligned .form-controls {
		margin-left: 220px;
	}
}

/* buttons
----------------------------------------------------------------- */

.button {
    /* Structure */
    display: inline-block;
    *display: inline; /*IE 6/7*/
    zoom: 1;
    line-height: normal;
    white-space: nowrap;
    vertical-align: baseline;
    text-align: center;
    cursor: pointer;
    user-select: none;
}
/* Firefox: Get rid of the card focus border */
.button::-moz-focus-card {
    padding: 0;
    border: 0;
}
.button {
	font-size: $font-size-h6;
    *font-size: 90%; /*IE 6/7 - To reduce IE's oversized button text*/
    *overflow: visible; /*IE 6/7 - Because of IE's overly large left/right padding on buttons */
    padding: 0.9em 15px;
    color: $color-base;
    border: 1px solid #999;  /*IE 6/7/8*/
    border: 2px solid $color-base;  /*IE9 + everything else*/
    background-color: transparent;
    text-decoration: none;
	//transition: all 0.2s ease;
}
.button-hover,
.button:hover,
.button:focus {
	// color: $color-blue;
	// border-color: $color-blue;
}
.button:focus {
    outline: 0;
}
.button-active,
.button:active {
}
.button[disabled],
.button-disabled,
.button-disabled:hover,
.button-disabled:focus,
.button-disabled:active {
    border: none;
    background-image: none;
    filter: progid:DXImageTransform.Microsoft.gradient(enabled = false);
    filter: alpha(opacity=40);
    opacity: 0.40;
    cursor: not-allowed;
    box-shadow: none;
}
.button-hidden {
    display: none;
}
/* Firefox: Get rid of the card focus border */
.button::-moz-focus-card{
    padding: 0;
    border: 0;
}
.button-primary,
.button-selected,
a.button-primary,
a.button-selected {
    background-color: $color-button-primary-bg;
    color: $color-button-primary-text;
}

/* Placeholder text */

::-webkit-input-placeholder {
   color: $color-placeholder-text;
}
:-moz-placeholder { /* Firefox 18- */
   color: $color-placeholder-text;
}
::-moz-placeholder {  /* Firefox 19+ */
   color: $color-placeholder-text;
}
:-ms-input-placeholder {
   color: $color-placeholder-text;
}
